import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Row, Col, Form, Button, Drawer, DatePicker } from "antd";
import moment, { min } from "moment";
import {
  isDrawerVisible,
  FilterInputChangeValue,
  getRtoList,
  setRtoParams,
} from "../../../../../../store/sideMenu/rto/rtoSlice";

var date = new Date(),
  y = date.getFullYear(),
  m = date.getMonth();

const filter = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {}, []);
  const onFinish = () => {
    props.setRtoParams({
      ...props.rtoparams,
      start_date: props.rtoparams.start_date,
      end_date: props.rtoparams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getRtoList({
      ...props.rtoparams,
      // start_date: props.rtoparams.start_date != "" ? props.rtoparams.start_date.format("YYYY-MM-DD") : "",
      // end_date: props.rtoparams.end_date != "" ? props.rtoparams.end_date.add(1, "days")
      // .format("YYYY-MM-DD") : "",
      // start_date:
      //   props.rtoparams.start_date != null && props.rtoparams.start_date != ""
      //     ? moment(props.rtoparams.start_date).format("YYYY-MM-DD")
      //     : "",
      // end_date:
      //   props.rtoparams.end_date != null && props.rtoparams.end_date != ""
      //     ? moment(props.rtoparams.end_date).add(1, "days").format("YYYY-MM-DD")
      //     : "",
      start_date: props.rtoparams.start_date,
      end_date: props.rtoparams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });
    props.isDrawerVisible(false);
  };

  function clearFilter() {
    props.setRtoParams({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
    props.getRtoList({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
  }
  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.rtoparams?.start_date,
      end_date: props.rtoparams?.end_date,
      // counter: props.rtoparams?.counter,
      // insurancecategory: props.rtoparams?.insurancecategory,
      // insurancestatus: props.rtoparams?.insurancestatus,
    });
  }, [props.rtoparams]);

  function onSearch(val) {
    props.getRtoList({ page: 1, search: val, page_size: 10 });
  }
  return (
    <Row gutter={[24, 24]}>
      <Col span={24}>
        <Drawer
          title="Filters"
          width={420}
          className="hp-drawer-mobile"
          onClose={() => {
            props.isDrawerVisible(false);
          }}
          placement="right"
          visible={props.drawer}
          bodyStyle={{
            paddingBottom: 10,
          }}
        >
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col span={12}>
                <Form.Item label="From Date" name="start_date">
                  <DatePicker
                    style={{ width: "100%" }}
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: new Date(),
                    })}
                    onChange={(date) => {
                      props.FilterInputChangeValue({
                        key: "start_date",
                        value: date == null ? "" : date,
                      });
                      if (date == null) {
                        props.FilterInputChangeValue({
                          key: "end_date",
                          value: "",
                        });
                      }
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="To Date" name="end_date">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment().format("YYYY-MM-DD"),
                      startDate: moment(props.rtoparams?.start_date).format(
                        "YYYY-MM-DD"
                      ),
                    })}
                    disabled={
                      props.rtoparams.start_date == null ||
                      props.rtoparams.start_date == ""
                        ? true
                        : false
                    }
                    onChange={(date) => {
                      props.FilterInputChangeValue({
                        key: "end_date",
                        value: date == null ? "" : date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col md={12}>
                <Button
                  block
                  type="secondary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    clearFilter();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
              </Col>
              <Col md={12}>
                <Button
                  block
                  type="primary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Apply
                </Button>
              </Col>
            </Row>
          </Form>
        </Drawer>
      </Col>
    </Row>
  );
};

function mapStateToProps({ rto }) {
  return {
    drawer: rto.drawer,
    rtoparams: rto.rtoparams,
  };
}

export default connect(mapStateToProps, {
  getRtoList,
  isDrawerVisible,
  setRtoParams,
  FilterInputChangeValue,
})(filter);
