import { Fragment, useEffect } from "react";
import { connect } from "react-redux";
import { useHistory } from "react-router";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Row, Col, Select, Upload } from "antd";
import NumericInput from "../../../../components/data-entry/inputs/numberInput";
import {
  isModelVisible,
  InputChangeValue,
  rtoAdd
} from "../../../../../store/sideMenu/rto/rtoSlice";

const addRto = (props) => {
  const [form] = Form.useForm();
  const history = useHistory();
  useEffect(() => {

  }, []);

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // Adds New Country

  // const handleAdd = () => {
  //   const rtoObj = {
  //     ...props.rtoData,
  //     id: id,
  //   };
  //   props.rtoAdd(rtoObj);
  // };

  // Updates Country in Store


  const handleUpdate = () => {
    const rtoObj = {
      ...props.rtoUpdateData,
    }
    props.rtoAdd({ rtoObj, id: props.rtoData.id, });
  };

  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    return (
      <Fragment>
        <Button
          type="primary"
          onClick={() => {
            form.submit();
          }}
          block>
          Update
        </Button>
      </Fragment>
    );

  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      Update RTO Amount
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      amount: props.rtoUpdateData?.amount,
    });
  }, [props.rtoUpdateData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          handleUpdate();
        }}>
        <Form.Item label="Amount :"
          name="amount"
          rules={[
            {
              required: true,
              message: "Please input your Amount!"
            },
          ]}
          getValueProps={() => { }}
          hasFeedback>
          <NumericInput
            id="amount"
            name="amount"
            style={{ width: "100%" }}
            maxLength={50}
            placeholder="amount"
            value={
              props.rtoUpdateData.amount == null
                ? ""
                : props.rtoUpdateData?.amount
            }
            onChange={props.InputChangeValue}
          />
        </Form.Item>

      </Form>
    </Modal >
  );
};

function mapStateToProps({ rto, }) {
  return {
    rtoData: rto.rtoData,
    rtoUpdateData: rto.rtoUpdateData,
    model: rto.model,

  };
}

export default connect(mapStateToProps, {
  isModelVisible,
  InputChangeValue,
  rtoAdd
})(addRto);
