import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  List,
  Typography,
  Button,
  Input,
  Card,
  Pagination,
  Spin,
  Table,
  Popconfirm,
  Space,
} from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import {
  OrderedListOutlined,
  AppstoreOutlined,
  PlusOutlined,
  FilterOutlined,
  EditOutlined,
} from "@ant-design/icons";
import PageTitle from "../../../../../layout/components/content/page-title";
import EmptyImage from "../../../../../assets/images/app/empty-list.svg";
import moment from "moment";
import {
  getRtoList,
  setRtoParams,
  isModelVisible,
  isDrawerVisible,
  getRtoById,
} from "../../../../../store/sideMenu/rto/rtoSlice";
import { isMobile } from "react-device-detect";
import Filter from "./Filters/Filter";
import AddRto from "./addRto";

function newRto(props) {
  const { Search } = Input;
  const { Text,  } = Typography;

  useEffect(() => {
    // props.getRtoList({ ...props.rtoparams, page: 1, search: "", page_size: 10 });

    props.getRtoList({ ...props.rtoparams, page: 1 });
    props.setRtoParams({ ...props.rtoparams, page: 1 });
  }, []);

  function onPageChange(page, pageSize) {
    props.setRtoParams({
      ...props.rtoparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getRtoList({
      ...props.rtoparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.setRtoParams({ ...props.rtoparams, page: 1, search: val });
    props.getRtoList({ ...props.rtoparams, page: 1, search: val });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  const data = props.newRtoList?.map((row, key) => ({
    ...row,
    // index: key + 1,
    index: (props.rtoparams.page - 1) * props.rtoparams.page_size + (key + 1),
  }));

  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 50,
      fixed: isMobile ? null : "left",
    },
    {
      title: "Date",
      render: (row) => (
        <span className={columnClass}>{moment(row.createdon).format("DD-MM-YYYY")}</span>
      ),
      width: 100,
    },
    {
      title: "Code",
      width: 80,
      render: (row) => (
        <span className={columnClass}>{row.code}</span>
      ),
    },

    {
      title: "Customer Name",
      width: 100,
      render: (row) => (
        <span className={columnClass}>
          {row.customer?.firstname} {row.customer?.middlename}{" "}
          {row.customer?.lastname}
        </span>
      ),
    },
    {
      title: "Customer Mobile",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.customer?.mobile}</span>
      ),
    },

    {
      title: "Vehicle Number",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.vehicle?.vehicleno}</span>
      ),
    },

    {
      title: "Purchase / Sale Code",
      width: 130,
      render: (row) => (
        <span className={columnClass}>
          <>{row.rtatype == 1 ? row.purchase?.code : row.sale?.code}</>
        </span>
      ),
    },
    {
      title: "Amount",
      width: 60,
      render: (row) => <span className={columnClass}>{row.amount}</span>,
    },
    {
      title: "RTO Status",
      width: 100,
      render: (row) => (
        <span className={columnClass}>
          {
            <Text
              type={
                row.rtastatus == 3 
                  ? "success"
                  : row.rtastatus == 1
                  ? "danger"
                  : "warning"
              }
            >
              {row.rtastatus_name == null
                ? "Pending"
                : row.rtastatus_name}
            </Text>
          }</span>
      ),
    },
    {
      title: "RTO Type",
      width: 100,
      render: (row) => <span className={columnClass}>{row.rtatype_name}</span>,
    },
    {
      title: "Processed By",
      width: 80,
      render: (row) => (
        <span className={columnClass}>{row.processedby?.fullname}</span>
      ),
    },
    {
      title: "Processed On",
      width: 80,
      render: (row) => (
        <span className={columnClass}>
          {
          row.processedon != null ?  
          moment(row.processedon).format("DD-MM-YYYY") :
          ""}
          </span>
      ),
    },
    {
      title: "Actions",
      key: "action",
      width: 50,
      fixed: isMobile ? null : "right",
      render: (row, record) => (
        <>
          {props.userAccessList?.indexOf(
            "InsuranceRto.change_rta"
          ) !== -1 && (
            <Space size="middle">
              <>
                {row.rtastatus == 1 && (
                  <>
                    {/* <Button
                type="primary"
                className="hp-w-100 hp-btn-outline hp-hover-bg-primary-1"
                htmlType="submit"
                onClick={() => {
                  props.getRtoById(row.id);
                  props.isModelVisible(true);
                }}>Update</Button> */}
                    <EditOutlined
                      className="ri-add-line hp-mr-2 "
                      style={{ cursor: "pointer", color: "#fc6f03" }}
                      onClick={() => {
                        props.getRtoById(row.id);
                        props.isModelVisible(true);
                      }}
                    />
                  </>
                )}
              </>
            </Space>
          )}
        </>
      ),
    },
  ];

  return (
    <Row gutter={[12, 12]}>
      <PageTitle pageTitle="RTO" goBack={false} />
      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          <Col
            span={24}
            xs={18}
            sm={18}
            md={14}
            className="hp-ecommerce-app-header-search "
          >
            <Search
              placeholder="Search for date, doc, number, customer, counter, vehicle"
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getRtoList({
                    ...props.rtoparams,
                    page: 1,
                    search: "",
                  });
                  props.setRtoParams({
                    ...props.rtoparams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>
          <Col span={24} xs={6} sm={6} md={3}>
            <Button
              onClick={() => {
                props.isDrawerVisible(true);
              }}
              className="hp-w-100 hp-p-12"
              icon={<FilterOutlined className="hp-mr-10" />}
            >
              {!isMobile && "Filter"}
            </Button>
          </Col>
        </Row>
      </Col>
      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  rowClassName="hp-btn-bg-color-none"
                  dataSource={data}
                  scroll={{ x: 1300 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.rtoparams?.page}
                pageSize={props.rtoparams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddRto />
      <Filter />
    </Row>
  );
}
function mapStateToProps({ rto, system }) {
  return {
    newRtoList: rto.newRtoList,
    rtoparams: rto.rtoparams,
    noofpages: rto.noofpages,
    listCount: rto.listCount,
    loading: rto.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getRtoList,
  setRtoParams,
  isModelVisible,
  isDrawerVisible,
  getRtoById,

  // isInsuranceViewModelVisible,
  // getInsuranceById
})(newRto);
